import { mapMutations } from 'vuex';
export default {
  name:'imgs',
  props:['carid'],
  data:function(){
    return{
      list:[]
    }
  },
  methods:{
    ...mapMutations(['mutationImgID']),
    // 
    toimgmore:function(imgid){
      this.$router.push('/imgmore/' + this.carid);
      // localStorage
      window.localStorage.setItem('imgid',imgid);
      // mutation
      this.mutationImgID(imgid);
    }
  },
  beforeRouteEnter:function(to, from, next){
    next(function(vm){
      vm.$http.get('/v2-car-getImageList.html?SerialID=' + vm.carid).then(function(response){
        vm.list = response.data.data;
      })
    })
  },
  beforeRouteUpdate:function(to, from, next){
    var app = this;
    this.$http.get('/v2-car-getImageList.html?SerialID=' + app.carid).then(function(response){
        app.list = response.data.data;
      })
    next();
  }
}



